/**
 * The CurrencySettings static object represents settings for the currency data type.
 * It provides digits for a specific currency and the default scale.
 */
var ScaleSettings = {};

/**
 * Update the default currency setting with customization.
 *
 * @param {Object} oConfig - A configuration object that is used to specify the scale settings.
 * @param {Object} oConfig.currency - A custom settings object that will be used for defining currency digits.
 * @param {Object} oConfig.unit - A map of units with their corresponding scale
 * @public
 * @static
 */
ScaleSettings.update = function(oConfig) {
	var sUnit, iScale;

	/* Update currency scale with custom currencies */
	if (oConfig.currency) {
		for (sUnit in oConfig.currency) {
			ScaleSettings.CURRENCY[sUnit] = oConfig.currency[sUnit].scale;
		}
	}

	/* Determine MAX_CURRENCY_SCALE */
	for (sUnit in ScaleSettings.CURRENCY) {
		iScale = ScaleSettings.CURRENCY[sUnit];

		if (iScale > ScaleSettings.MAX_CURRENCY_SCALE) {
			ScaleSettings.MAX_CURRENCY_SCALE = iScale;
		}
	}

	if (oConfig.unit) {
		for (sUnit in oConfig.unit) {
			ScaleSettings.UNIT[sUnit] = oConfig.unit[sUnit].scale;
		}
	}
};

/**
 * Defines the display default scale that is used for alignment. This has no effect on the technical scale of currency
 * values. It is only used for alignment purposes so that different currencies with different scale are still aligned
 * correctly.
 *
 * @type {number}
 */
ScaleSettings.MAX_CURRENCY_SCALE = 3;

ScaleSettings.CURRENCY = {
	'ADP': 0,
	'AFN': 0,
	'ALL': 0,
	'BHD': 3,
	'BIF': 0,
	'BYR': 0,
	'CLF': 4,
	'CLP': 0,
	'DEFAULT': 2,
	'DJF': 0,
	'ESP': 0,
	'GNF': 0,
	'HUF': 0,
	'IQD': 0,
	'IRR': 0,
	'ISK': 0,
	'ITL': 0,
	'JOD': 3,
	'JPY': 0,
	'KMF': 0,
	'KPW': 0,
	'KRW': 0,
	'KWD': 3,
	'LAK': 0,
	'LBP': 0,
	'LUF': 0,
	'LYD': 3,
	'MGA': 0,
	'MGF': 0,
	'MMK': 0,
	'MRO': 0,
	'OMR': 3,
	'PYG': 0,
	'RSD': 0,
	'RWF': 0,
	'SLL': 0,
	'SOS': 0,
	'STD': 0,
	'SYP': 0,
	'TMM': 0,
	'TND': 3,
	'TRL': 0,
	'TWD': 0,
	'UGX': 0,
	'UYI': 0,
	'UYW': 4,
	'VND': 0,
	'VUV': 0,
	'XAF': 0,
	'XOF': 0,
	'XPF': 0,
	'YER': 0,
	'ZMK': 0,
	'ZWD': 0
};

ScaleSettings.UNIT = {};

module.exports = ScaleSettings;
